.wrapper {
    display: flex;
    flex-direction: column;
}

.wrapper a {
    text-decoration: none;
    color: burlywood;
}

.header img {
    margin: 10px 15px 0 10px;
    width: 200px;
    height: 200px;
    border-radius: 50% 20% / 10% 40%;;
}

.contactItem {
    margin: 0;
    display: flex;
    flex-direction: column;
}

.contactIcon {
    color: burlywood;
}

.skills {
    margin: 0;
    display: flex;
    flex-direction: column;
}

.skillBlock {
    display: flex;
    margin: 0;
}

.skillBlock ul {
    list-style-type: none;
    padding: 0;
}

.interests {
    display: flex;
    flex-direction: column;
    margin: 0;
}

.contactIcon img {
    width: 20px;
    height: 20px;
    background-color: burlywood;
}

.contactText {

}

.radioContainer {
    display: flex;
}

.on {
    background-color: green;
}

.off {
    background-color: red;
}
